<template>
    <div class="mypage-container">
        <div class="mypage-title pc-element">비밀번호 변경</div>
        <form novalidate @submit.prevent="validate">
            <ul class="input-ul" style="padding-top:30px;">
                <li>
                    <div class="label">새 비밀번호</div>
                    <div class="input-box">
                        <v-text-field
                                outlined
                                ref="newPass"
                                type="password"
                                :rules="[checkNewPass]"
                                v-model="newPass"/>
                    </div>
                </li>
                <li>
                    <div class="label">새 비밀번호 확인</div>
                    <div class="input-box">
                        <v-text-field
                                outlined
                                ref="reNewPass"
                                type="password"
                                :rules="[checkReNewPass]"
                                v-model="reNewPass"/>
                    </div>
                </li>
            </ul>
            <div class="button-row">
                <v-btn type="submit" class="blackButton">
                    <span>저장</span>
                </v-btn>
            </div>
        </form>

    </div>
</template>
<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_UPDATE_EMAIL_AUTH_PASSWORD,
    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'
    import {mapGetters} from "vuex";
    import JQ from "jquery"

    export default {
        name: 'emailModifyPassword',
        computed: {
            form() {
                return {
                    newPass: this.newPass,
                    reNewPass: this.reNewPass,
                }
            },

        },
        data() {
            return {
                newPass: '',
                reNewPass: '',
                uidEncode:'',
                serverFlag: false,
            };
        },
        created() {
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.$router.push('/');
            }
            this.uidEncode = this.$route.params.code;
            if(!this.uidEncode) {
                this.$router.push('/');
            }
        },
        methods: {
            checkNewPass() {
                if (!this.newPass) return '새 비밀번호를 입력하세요'
                if (this.newPass.length < 6 || this.newPass.length > 20) return '비밀번호는 6~20자 사이로 입력하세요'

                let reg = /\d{1}/;
                let ret = reg.test(this.newPass)
                if (!ret) {
                    return '비밀번호는 한개이상의 숫자를 포함해야 합니다'
                }

                reg = /[a-zA-Z]/;
                ret = reg.test(this.newPass)
                if (!ret) {
                    return '비밀번호는 영문을 포함해야 합니다'
                }

                reg = /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣]/;
                ret = reg.test(this.newPass)
                if (ret) {
                    return '비밀번호는 한글을 포함해서는 안됩니다'
                }
                return true;
            },
            checkReNewPass() {
                if (!this.reNewPass) return '새 비밀번호 확인를 입력하세요'
                if (this.reNewPass != this.newPass) return '새 비밀번호와 새 비밀번호 확인이 일치하지 않습니다'
                return true;
            },
            validate() {
                let formHasErrors = false;
                let frm = {};
                Object.keys(this.form).forEach(f => {
                    let error = this.$refs[f].validate(true)
                    if (!error) {
                        formHasErrors = true
                    } else {
                        frm[f] = this.form[f];
                    }
                })
                if (!formHasErrors) {
                    if (this.serverFlag) return;
                    frm.uidEncode = this.uidEncode;

                    this.$eventBus.$emit('overlay', 'open');
                    this.serverFlag = true;
                    apiService.post(API_URL_UPDATE_EMAIL_AUTH_PASSWORD, frm, (res) => {
                        this.serverFlag = false;
                        this.$eventBus.$emit('overlay', 'hide');
                        let result = res.status;
                        let params = {}
                        if (result == 'success') {
                            params = {
                                message: '비밀번호가 변경되었습니다',
                                doAction:()=> {
                                    this.$router.push('/memberLogin')
                                }
                            };
                            this.$eventBus.$emit('modalOpen', params);
                        }



                    })

                }
            },
        },
        mounted() {

            JQ('.out-text p').css({
                'margin': 0,
                'font-size': '14px',
                'color': '#333',
                'font-weight': '500'
            });
        },
        updated() {

            JQ('.out-text p').css({
                'margin': 0,
                'font-size': '14px',
                'color': '#333',
                'font-weight': '500'
            });
        }

    }
</script>


<style lang="scss" scoped>
    .mypage-container {
        width: 636px;
        margin: 0 auto;
        padding: 36px 24px 50px 24px;
        min-height: calc(100vh - 300px);
        background: #fff;

        .mypage-title {
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            border-bottom: solid 1px #333;
            padding-bottom: 10px;
        }

        .button-row {
            width: 86px;
            margin: 10px auto 40px auto;

            .blackButton {
                max-height: 38px !important;

                span {
                    font-size: 14px;
                }
            }

        }

        .out-text {

            font-size: 14px;
            color: #333;
            font-weight: 500;
            padding-bottom: 10px;

        }

        .check-box {
            margin-top: -10px;

            ::v-deep .v-label {
                font-size: 14px;
                color: #333;
                margin-left: -5px;
            }
        }

        .input-ul {
            padding: 0 20px;
            margin: 10px 0 0 0;

            li {
                display: flex;
                justify-content: space-between;

                .label {
                    min-width: 120px;
                    padding: 15px 0 0 0;
                    font-weight: 600;
                    color: #333;
                    font-size: 14px;

                }

                .input-box {
                    text-align: left;
                    width: 95%;

                }

                .radio-box {
                    text-align: left;
                    width: 95%;
                    padding-bottom: 10px;
                }

                .switch-box {
                    width: 95%;
                    display: flex;
                    justify-content: space-between;

                    .tip {
                        width: 99%;
                        font-size: 12px;
                        color: #333333;
                        padding-top: 15px;
                    }

                    .switch {
                        text-align: right;
                        min-width: 30px;
                        margin-top: -6px;
                    }
                }
            }
        }

    }
    @media screen and (max-width: 1259px) {
        .mypage-container {
            width: 100%;
            padding: 0;


            .input-ul {
                padding: 0;
                margin: 10px 0 0 0;

                li {
                    display: block;
                    .label {
                        padding:0 0 10px 0;
                    }

                    .input-box {
                        width:100%;

                    }


                }

            }

        }
    }
    @media screen and (max-width: 450px) {
        .mypage-container {
            padding: 0 20px;


        }
    }
</style>
